import React from 'react'
import {useState,useEffect} from 'react'
import {FormattedMessage} from 'gatsby-plugin-intl'
import ReactTable from 'react-table'
import 'react-table/react-table.css'
import Cookies from 'js-cookie'
import Web3 from 'web3'
import defaultVars from '../../libs/config'
import contractAbi from '../../libs/abi'
const prices = defaultVars.prices
const web3 = new Web3(defaultVars.provider);
const contract = new web3.eth.Contract(contractAbi,defaultVars.contractAddress);
let items = [
    {id: 'stats.txtm',msg: 'Transactions to me'},
    {id: 'stats.mtx',msg: 'My transactions'},
    {id: 'stats.ext',msg: 'Bonus profit'},
    {id: 'stats.lost',msg: 'Lost profit'},
]
const columns = [{
    Header: props => <FormattedMessage id="stats.from" defaultMessage="From"/>,
    accessor: 'from', // String-based value accessors!,
    Cell: props => <a href={`${defaultVars.linktxs}/${props.value.tx}`} className="" target="_blank">{props.value.address}</a>
    }, {
    Header: props => <FormattedMessage id="stats.amount" defaultMessage="Amount"/>,
    accessor: 'lv',
    Cell: props => <span className='number'>{prices[props.value-1]}</span> // Custom cell components!
    }, {
    Header: props => <FormattedMessage id="stats.lv" defaultMessage="Level"/>,
    accessor: 'lv' // Custom value accessors!
    }, {
    Header: props => <FormattedMessage id="stats.date" defaultMessage="Date"/>,
    accessor: 'date',
    Cell: props => {
        const t = new Date(Number(props.value*1000))
        return `${t.getFullYear()}-${("0" + (t.getMonth()+1)).slice(-2)}-${("0" + t.getDate()).slice(-2)} ${("0"+t.getHours()).slice(-2)}:${("0" + t.getMinutes()).slice(-2)}`;
    }
}]
const columnsBonus = [{
    Header: props => <FormattedMessage id="stats.lv" defaultMessage="Level"/>,
    accessor: 'l' // Custom value accessors!
    }, {
    Header: props => <FormattedMessage id="stats.date" defaultMessage="Date"/>,
    accessor: 't',
    Cell: props => {
        const t = new Date(Number(props.value*1000))
        return `${t.getFullYear()}-${("0" + (t.getMonth()+1)).slice(-2)}-${("0" + t.getDate()).slice(-2)} ${("0"+t.getHours()).slice(-2)}:${("0" + t.getMinutes()).slice(-2)}`;
    }
}]
const columnsmy = [{
    Header: props => <FormattedMessage id="stats.to" defaultMessage="To"/>,
    accessor: 'to', // String-based value accessors!,
    Cell: props => <a href={`${defaultVars.linktxs}/${props.value.tx}`} className="" target="_blank">{props.value.address}</a>
    }, {
    Header: props => <FormattedMessage id="stats.amount" defaultMessage="Amount"/>,
    accessor: 'lv',
    Cell: props => <span className='number'>{prices[props.value-1]}</span> // Custom cell components!
    }, {
    Header: props => <FormattedMessage id="stats.lv" defaultMessage="Level"/>,
    accessor: 'lv' // Custom value accessors!
    }, {
    Header: props => <FormattedMessage id="stats.date" defaultMessage="Date"/>,
    accessor: 'date',
    Cell: props => {
        const t = new Date(Number(props.value*1000))
        return `${t.getFullYear()}-${("0" + (t.getMonth()+1)).slice(-2)}-${("0" + t.getDate()).slice(-2)} ${("0"+t.getHours()).slice(-2)}:${("0" + t.getMinutes()).slice(-2)}`;
    }
}]
const Stats = () => {
    const [activeTab,setActiveTab] = useState(0);
    const [pay,setPay] = useState([]);
    const [receive,setReceive] = useState([]);
    const [lost,setLost] = useState([]);
    const [bonus,setBonus] = useState([]);
    useEffect(()=>{
        const address = Cookies.get('user');
        const a = async () => {
            let payListMin = [];
            const payList = await contract.getPastEvents("allEvents",{fromBlock: 0,topics:[["0x07c3121cf5ba6f12de77662eba4264c329ab040b9912576221c15ee6f097d96a"],null,`0x000000000000000000000000${address.slice(2,)}`]});
            for (let i of payList){
                payListMin.push({lv: Number(i.returnValues._l.toString()),to:{address: i.returnValues._u,tx: i.transactionHash},date: i.returnValues._t.toString(),tx: i.transactionHash})
            }
            setPay(payListMin)
        }
        const b = async () => {
            let receiveListMin = [];
            const receiveList = await contract.getPastEvents("allEvents",{fromBlock: 0,topics:["0x07c3121cf5ba6f12de77662eba4264c329ab040b9912576221c15ee6f097d96a",`0x000000000000000000000000${address.slice(2,)}`]});
            for (let i of receiveList){
                receiveListMin.push({from: {address: i.returnValues._r,tx: i.transactionHash}, lv: i.returnValues._l.toString(),date: i.returnValues._t.toString(),tx: i.transactionHash});
            }
            setReceive(receiveListMin);
        }
        const c = async () => {
            let lostListMin = [];
            const lostList = await contract.getPastEvents("allEvents",{fromBlock: 0,topics:["0xfd9c220cc55904bb20450834d06c3feb3e87a15d81545ec87cc0e15261715061",`0x000000000000000000000000${address.slice(2,)}`]});
            for (let i of lostList){
                lostListMin.push({from: {address: i.returnValues._r,tx:i.transactionHash},lv: i.returnValues._l.toString(),date: i.returnValues._t.toString(),tx: i.transactionHash});
            }
            setLost(lostListMin);
        }
        const d = async () => {
            let bonusListMin = [];
            const bonusList = await contract.getPastEvents("allEvents",{fromBlock: 0,topics: ["0x154bd3f0184bafa2f1300062924f0db714a9bf2cc5ff17ee8fbf5bfa9471ee16",`0x000000000000000000000000${address.slice(2,)}`]});
            for (let i of bonusList){
                bonusListMin.push({l: i.returnValues._l.toString(),t: i.returnValues._t.toString()})
            }
            setBonus(bonusListMin);
        }
        a();
        b();
        c();
        d();
    },[])
    return (
        <div className="px-2 py-2 lg:px-8 lg:py-8 shadow bg-white">
            <div className="text-16 text-lights-orangebold uppercase font-sans font-bold text-center mb-8">
                <FormattedMessage id="db.nav.stats" defaultMessage="Stats"/>
            </div>
            <div className="text-center mb-4">
                {items.map((val,key)=>{
                    return (
                         <button onClick={()=>setActiveTab(key)} key={key} className={`focus:outline-none text-14 font-sans font-semibold px-4 py-2 w-full rounded md:w-auto mr-4 border mb-2 md:mb-0 ${key===activeTab ? 'border-lights-bluebold text-lights-orangebold bg-lights-bluebold':'border-lights-grayblack text-lights-bluebold bg-white'}`}>
                             <FormattedMessage id={val.id} defaultMessage={val.msg}/>
                         </button>
                    )
                })}
            </div>
            {activeTab === 0 ? <div className="">
                <div className="text-center mb-4 text-16 text-lights-bluebold font-semibold">
                    <FormattedMessage id="stats.txtmd" defaultMessage="Transactions from referrals to me"/>
                </div>
                <ReactTable
                    data={receive}
                    columns={columns}
                />
            </div>:null}
            {activeTab === 1 ? <div className="">
                <div className="text-center mb-4 text-16 text-lights-bluebold font-semibold">
                    <FormattedMessage id="stats.mtxd" defaultMessage="My transactions to the uplines"/>
                </div>
                <ReactTable
                    data={pay}
                    columns={columnsmy}
                />
            </div>:null}
            {activeTab === 2 ? <div className="">
                <div className="text-center mb-4 text-16 text-lights-bluebold font-semibold">
                    <FormattedMessage id="stats.extd" defaultMessage="My bonus profit"/>
                </div>
                <ReactTable
                    data={bonus}
                    columns={columnsBonus}
                />
            </div>:null}
            {activeTab === 3 ? <div className="">
                <div className="text-center mb-4 text-16 text-lights-bluebold font-semibold">
                    <FormattedMessage id="stats.lostd" defaultMessage="Missed transactions"/>
                </div>
                <div className="font-sans text-14 text-lights-bluebold mb-4">
                    <div className="">
                        <FormattedMessage id="stats.lostd1" defaultMessage="Profit loss - is a purchase of a level by your referral of the appropriate line, if you haven't reached this level yet. In this case the funds will be directed to Your upline of the appropriate line, from whom you can buy this level."/>
                    </div>
                    <div className="">
                        <FormattedMessage id="stats.lostd2" defaultMessage="For example. Referral of the 3rd line wants to buy the 3rd level from You but you still haven't bought the 2nd level. In this case the money will be directed to your upline of the 3rd line."/>
                    </div>
                    <div className="">
                        <FormattedMessage id="stats.lostd3" defaultMessage="If your level is active, then even if your referral buys this level many times in a row for many years to come, all this money goes to you."/>
                    </div>
                    <div className="">
                        <FormattedMessage id="stats.lostd4" defaultMessage="If you have inactive referrals, then all the money that should have gone to them for the corresponding levels goes to you."/>
                    </div>
                </div>
                <ReactTable
                    data={lost}
                    columns={columns}
                />
            </div>:null}
        </div>
    )
}

export default Stats